import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Row,
  Col
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getNowPageId} from '../../global/components/menuData'

@connect(
  state => ({...state.comAdInquiry, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  // 导出门店列表
  export = () => {
    const {handleExport, searchParams, userLoation} = this.props
    handleExport({searchParams: {...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}, userLoation}, () => {
      message.success('导出成功')
    })
  }

  render() {
    const {comAdInquiryExport} = this.props
    return (
      <div className={style['table-tool']}>
        {
          comAdInquiryExport && <Row type="flex" justify="start">
          <Col span={12}>
            <Button type="primary" className={style.mr10} onClick={this.export} >导出</Button>
          </Col>
        </Row>
        }
      </div>
    )
  }
}



