// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.COMADINQUIRY_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  if (!reqObj.startTime) {
    reqObj.startTime = moment(new Date()).format('YYYY-MM-DD')
  } else {
    reqObj.startTime = moment(reqObj.startTime).format('YYYY-MM-DD')
  }
  getData({api: API_PRODUCTNEARPERIOD_LOADPAGE_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.COMADINQUIRY_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.COMADINQUIRY_FILE
    })
  })
}

// 导出商品列表
export const handleExport = (reqData, callBack) => (dispatch) => {
  let api = API_PRODUCTNEARPERIOD_EXPORT
  let reqObj = reqData.searchParams
  if (!reqObj.startTime) {
    reqObj.startTime = moment(new Date()).format('YYYY-MM-DD')
  } else {
    reqObj.startTime = moment(reqObj.startTime).format('YYYY-MM-DD')
  }
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (!reqObj.warehouseCode) {
    reqObj.warehouseCode = reqData.userLoation && reqData.userLoation.value
  }
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.COMADINQUIRY_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.COMADINQUIRY_FILE
    })
  })
}

//商品分类中类，小类
export const productCenterOrSmall = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.COMADINQUIRY_UPDATE})
  let api = API_PRODUCT_CATEGORYSONS
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.COMADINQUIRY_UPDATE,
      data: {

      }
    })
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.COMADINQUIRY_FILE
    })
  })
}
// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.COMADINQUIRY_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.COMADINQUIRY_FILE
    })
  })
}



