import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import { update } from '../../storePriority/redux/action';

@connect(
  state => ({...state.comAdInquiry, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '仓库',
      width: 150,
      dataIndex: 'warehouseCode',
      key: 'warehouseCode',
    }, {
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    },{
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '货主',
      width: 150,
      dataIndex: 'shipperName',
      key: 'shipperName'
    }, {
      title: '临保期限',
      width: 200,
      dataIndex: 'tempPeriodTypeDesc',
      key: 'tempPeriodTypeDesc'
    }, {
      title: '入库批次',
      width: 150,
      dataIndex: 'batchCode',
      key: 'batchCode'
    }, {
      title: '库存',
      width: 150,
      dataIndex: 'qty',
      key: 'qty'
    }, {
      title: '生产日期',
      width: 200,
      dataIndex: 'productionTime',
      key: 'productionTime'
    },, {
      title: '保质期天数',
      width: 200,
      dataIndex: 'shelfLife',
      key: 'shelfLife'
    }, {
      title: '过期日期',
      width: 200,
      dataIndex: 'expirationTime',
      key: 'expirationTime'
    },, {
      title: '到货日期',
      width: 200,
      dataIndex: 'arriveTime',
      key: 'arriveTime'
    }]
  }


  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value ) return
    handleGetList({searchParams: {page, size, ...searchParams}, userLoation})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}, userLoation: newProps.userLoation})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, isAsc: true, ...searchParams, columns: sorter.field}, userLoation})
      } else {
        handleGetList({searchParams: {page, size, isAsc: false, ...searchParams, columns: sorter.field}, userLoation})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, ...searchParams, size: pagination.pageSize}, userLoation})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



