import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reqManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
      
    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'requisitionsQty',
      key: 'requisitionsQty'
    }, {
      title: '类型',
      width: 100,
      dataIndex: 'productPrice',
      key: 'productPrice'
    }, {
      title: '库区',
      width: 100,
      dataIndex: 'operateId',
      key: 'operateId'
    }, {
      title: '储位',
      width: 100,
      dataIndex: 'aaaaa',
      key: 'aaaaa'
    }, {
      title: '数量',
      width: 100,
      dataIndex: 'bbbbb',
      key: 'bbbbb'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'cccccc',
      key: 'cccccc'
    }, {
      title: '总金额（元）',
      width: 150,
      dataIndex: 'dddd',
      key: 'dddd'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, orderCode} = this.props
    handleGetDetailRecord({searchParams: {orderCode : orderCode}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowRecordModal: false})
  }

  render() {
    const {recordLoading, 
      searchResultRecord,
      orderCode,
      shipperName,
      requisitionsTypeName,
      requisitionsTotalAmount,
      remark, 
    } = this.props
    return (
        <Modal
          title="原料转成品单详情"
          visible={true}
          onCancel={this.handleCloseModal}
          footer={null}
          mask={false}
          width={1024}
        >
          <div style={{paddingLeft: 20}}>
            <Row>
              <Col span={12}><h4>货主：{shipperName}</h4></Col>
              <Col span={12}><h4>备用：{remark}</h4></Col>
            </Row>
          </div>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={recordLoading}
              columns={this.columns}
              scroll={{x: 900, y: 400}}
              dataSource={searchResultRecord}
              pagination={false}
            />
          </div>
        </Modal>
    )
  }
}



