import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  DatePicker,
  message
} from 'antd'
import moment from 'moment'
import style from './style/Form.scss'
import {TYPE_INSURANCE} from 'Mock'
import {CsxSelect, CsxSearch, CsxMultipleSearch, CsxMultipleSelect} from 'Form'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {
  API_USER_WAREHOUSELIST,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_PRODUCT_CATEGORYSONS,
  API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES,
  API_PRODUCTS_KEYVALUES
} from 'api'

@connect(
  state => ({...state.comAdInquiry, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  componentDidMount() {
    // const {handleGetList, searchParams, size, userLoation} = this.props
    // let reqObj = {...searchParams}
    // handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }

  // 查询数据
  query = () => {
    const {handleGetList, searchParams, userLoation, size} = this.props
    let reqObj = {...searchParams}
    reqObj.startTime = moment(reqObj.startTime).format('YYYY-MM-DD')
    handleGetList({searchParams: {...reqObj, page: 1, size}, userLoation})
  }

  // 重置
  reset = () => {
    const {update, searchParams} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      userLoation,
      searchParams: {
        startTime,
        warehouseCode,
        productCode,
        productName,
        // reservoirCode,
        // reservoirAreaTypeCode,
        // storageLocationCode,
        // storageLocationTypeCode,
        shipperCode,
        tempPeriodType,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode
      }
    } = this.props
    // 设置默认时间
    const dateFormat = 'YYYY-MM-DD'
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='仓库'
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.children}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES}
              label='货主名称'
              selectItem={{data: shipperCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shipperCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品编码"
              searchParams={{warehouseCode: userLoation.value}}
              searchParamsKey='productCode'
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          {/* <Col span={6}>
            <CsxSearch
              dateApi={API_RESERVOIRAREAS_GETRESERVOIRKEYVALUES}
              label="库区"
              width={163}
              searchParams={{ warehouseCode: userLoation.value }}
              searchParamsKey='reservoirCode'
              selectItem={{ data: reservoirCode, key: 'key', valueKey: ['key', 'value'] }}
              onChangeSelect={(value) => {
                this.onInputChange({ reservoirCode: value })
              }}
              onSelect={(value) => {
                this.onInputChange({ reservoirCode: value })
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_PRODUCTNEARPERIOD_RESERVOIRKEYVALUE}
              label='库区类型'
              // searchParams={{dicTypes: 'DATYPE'}}
              selectItem={{ data: reservoirAreaTypeCode, key: 'key', valueKey: 'value' }}
              onChangeSelect={(value) => {
                this.onInputChange({ reservoirAreaTypeCode: value })
              }}
            />
          </Col> */}
          {/* <Col span={6}>
            <CsxSearch
              dateApi={API_STOCKRELOCATION_GETSTORAGELOCATIONKEYVALUES}
              label="储位"
              width={163}
              searchParams={{ warehouseCode: userLoation.value, reservoirAreaCode: reservoirCode && reservoirCode.value }}
              searchParamsKey='storageLocationCode'
              selectItem={{ data: storageLocationCode, key: 'key', valueKey: ['key', 'value'] }}
              onChangeSelect={(value) => {
                this.onInputChange({ storageLocationCode: value })
                console.log(reservoirCode)
              }}
              onSelect={(value) => {
                this.onInputChange({ storageLocationCode: value })
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_STORAGELOCATIONS_WAREHOUSETYPES}
              label='储位类型'
              searchParams={{ type: 'T', operationType: 'S' }}
              selectItem={{ data: storageLocationTypeCode, key: 'key', valueKey: 'value' }}
              onChangeSelect={(value) => {
                this.onInputChange({ storageLocationTypeCode: value })
              }}
            />
          </Col> */}
          <Col span={6}>
            <div className='commonInput'>
              <label>开始日期：</label>
              <div className="box" style={{width: '60%'}}>
                <DatePicker
                  // style={{ width: '60%' }}
                  value={startTime}
                  placeholder={moment(new Date()).format('YYYY-MM-DD')}
                  format={dateFormat}
                  selectItem={{data: startTime}}
                  onChange={(value, moment) => {
                    console.log(value, 22222, moment, 111111)
                    this.onInputChange({startTime: value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={18}>
            <Row type="flex">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                placeholder='大类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxMultipleSelect
                placeholder='中类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value} ? {parCode: bigCategoryCode && bigCategoryCode.value, level: '3'} : {level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage='请先选择大类'
                style={{marginLeft: 10, width: 163}}
              />
              <CsxMultipleSelect
                placeholder='小类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                noReady
                errMessage='请先选择中类'
                style={{marginLeft: 10, width: 163}}
              />
            </Row>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='临保类型'
              data={TYPE_INSURANCE}
              selectItem={{data: tempPeriodType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({tempPeriodType: value})
              }}
            />
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



